@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';




























































































































































































































































































































































































.roleDialog {
  .el-tree
    > .el-tree-node
    > .el-tree-node__children
    > .el-tree-node
    > .el-tree-node__content {
    height: auto;
  }
  .el-tree
    > .el-tree-node
    > .el-tree-node__children
    > .el-tree-node
    > .el-tree-node__children
    .el-tree-node
    > .el-tree-node__content {
    height: auto;
  }
  .info-item {
    display: flex;
    align-items: center;
    width: 100%;
    .label {
      display: block;
      line-height: 26px;
      text-align: left;
      padding: 0 12px 0 0;
      box-sizing: border-box;
      width: 50%;

      .menu-title {
        display: inline-block;
        // width: 100px;
      }
    }
    .content {
      position: relative;
      width: 100%;
      max-height: 100px;
      margin-left: 20px;
      overflow: hidden;
      line-height: 32px;
      &.expand {
        max-height: none;
      }
      &-item {
        flex: 0 0 auto;
        padding: 2px 10px;
        margin-top: 20px;
        margin-right: 40px;
        border: 1px solid #ccc;
        border-radius: 5px;
      }
      &::before,
      &::after {
        display: table;
        content: '';
      }
      &::after {
        clear: both;
      }
      .box-col {
        width: 140px;
        height: 32px;
        margin-left: 20px;

        .el-checkbox {
          .el-checkbox__label {
            width: 135px !important;
            overflow: hidden !important;
            text-overflow: ellipsis !important;
            white-space: nowrap !important;
            vertical-align: middle !important;
          }
        }
      }
    }
    &-add {
      cursor: pointer;
      display: inline-block;
      width: 22px;
      margin-left: 10px;
      text-align: center;
      border: 1px solid rgb(55, 149, 211);
      border-radius: 3px;
    }

    &:before,
    &:after {
      display: table;
      content: '';
    }

    &:after {
      clear: both;
    }
  }
  .form-tree-wrap {
    max-height: 300px;
    overflow-y: scroll;
  }
}
